     $col-1: 100%/12;
     &__container {
          position: relative;
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          padding: 0;
          margin-left: auto;
          margin-right: auto;
          max-width: 100%;
          width: 100%;
          overflow: hidden;
          @include under(mobile) {
               flex-direction: column-reverse;
               justify-content: flex-start;
               height: auto;
          }
     }

     &__row {
          position: relative;
          width: 100%;
          min-height: 1px;
     }

     &__col {
          &-1-8 {
               width:  $col-1*8;
               @include under(mobile) {
                    width: 100%;
               }
          }

          &-1-3 {
               width:  $col-1*3;
               @include under(break) {
                    width:  $col-1*4;
               }
               @include under(mobile) {
                    width: 100%;
               }
          }
     }

	&__box {
		position:relative;
		width: 100%;
	}
}


// /*Grid Debug*/

.debug {
	margin-bottom: 20px;
	.grid {
		&__row {
			position:relative;
			background-color: yellow;
			min-height: 300px;
		}
		&__container {
			position:relative;
			background-color: red;
			min-height: 150px;

		}


		&__col-1-1,
		&__col-1-2,
		&__col-1-3 {
			position:relative;
			display: flex;
			&:nth-of-type(even) {
	 			background-color: green;
	 			min-height: 100px;
			}
			&:nth-of-type(odd) {
				background-color: pink;
	 			min-height: 100px;
			}
			&--contain-fixed {
				background-color: purple;
			}
		}

		&__col-1-1 {
			background-color: orange !important;
			min-height: 100px;
		}

		&__box {
			position: relative;
			background-color: blue;
			min-height: 50px;
			&--fixed {
				background-color: c();
			}
		}

		&__fixedElem {
			background-color: c();
			height: 200px;
		}
	}
}
